___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (max-width: 340px) {
.restaurant .restaurant__heading {
flex-direction: column;
gap: 15px;
}

.fav-btn {
width: 100%;
display: block;
}
}

@media screen and (min-width: 650px) {
.app-bar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
}

.app-bar .app-bar__brand h1 {
    font-size: 1.5em;
}

.app-bar .app-bar__menu {
    display: none;
}

.app-bar .app-bar__navigation {
    position: static;
    width: 100%;
}

.app-bar .app-bar__navigation ul li {
    display: inline-block;
}

.app-bar .app-bar__navigation ul li a {
    display: inline-block;
    width: 120px;
    text-align: center;
    margin: 0;
}

.restaurants {
    grid-template-columns: 1fr 1fr;
}

.restaurant {
    grid-template-columns: auto 1fr;
}

.restaurant .restaurant__title {
    text-align: center;
    font-size: 24px;
}

.restaurant .restaurant_overview {
    grid-column-start: 1;
    grid-column-end: 3;
}

.restaurant .restaurant__poster {
border-radius: 20px;
}
}

@media screen and (min-width: 800px) {
.app-bar .app-bar__brand h1 {
    font-size: 2em;
}
}

@media screen and (min-width: 850px) {
.restaurants {
    grid-template-columns: repeat(3, 1fr);
}
}

@media screen and (min-width: 1200px) {
.restaurants {
    grid-template-columns: repeat(4, 1fr);
}
}

@media screen and (min-width: 1600px) {
.restaurants {
    grid-template-columns: repeat(5, 1fr);
}
